import { combineReducers } from 'redux';
import { handleActions } from 'redux-actions';
import { addUser, deleteUser, editUser } from '../actions';
import { loadState } from '../../localStorage';

const stateFromLocalStorage = loadState();
const defaultState = (stateFromLocalStorage && stateFromLocalStorage.users) || {
  userList: [
    {firstName: '', middleName: '', lastName: '' },
    {firstName: '', middleName: '', lastName: '' },
    {firstName: '', middleName: '', lastName: '' },
    {firstName: '1', middleName: '1', lastName: '1' },
    {firstName: '2', middleName: '2', lastName: '2' },
    {firstName: '3', middleName: '3', lastName: '3' },
    {firstName: '4', middleName: '4', lastName: '4' },
    {firstName: '5', middleName: '5', lastName: '5' },
    {firstName: '6', middleName: '6', lastName: '6' },
    {firstName: '7', middleName: '7', lastName: '7' },
    {firstName: '8', middleName: '8', lastName: '8' },
    {firstName: '9', middleName: '9', lastName: '9' },
    {firstName: '10', middleName: '10', lastName: '10' },
    {firstName: '11', middleName: '11', lastName: '11' },
    {firstName: '12', middleName: '12', lastName: '12' },
    {firstName: '13', middleName: '13', lastName: '13' },
    {firstName: '14', middleName: '14', lastName: '14' },
  ]
};
const users = handleActions(
  {
    [addUser]: (state, action) => ({ ...state, userList: [...state.userList, action.payload]}),
    [editUser]: (state, action) => ({ ...state, ...state.userList.splice(action.payload.id, 1, action.payload.userObj)}),
    [deleteUser]: (state, action) => ({
      ...state,
      userList: [
        ...state.userList.slice(0, action.payload),
        ...state.userList.slice(action.payload + 1)
      ]}),
  },
  defaultState
);

export const getUserList = state => state.users;

export default combineReducers({ users });


